#container {
    overflow: hidden;
    height: 300px;
    position: relative;
}
#mapWrapper {
    width: 100%;
    height: 300px;
    z-index: 1;
}
#rvWrapper {
    width: 50%;
    height: 300px;
    top: 0;
    right: 0;
    position: absolute;
    z-index: 0;
}
#container.view_roadview #mapWrapper {
    width: 50%;
}
#roadviewControl {
    position: absolute;
    top: 5px;
    left: 5px;
    width: 42px;
    height: 42px;
    z-index: 1;
    cursor: pointer;
    background: url(https://t1.daumcdn.net/localimg/localimages/07/2018/pc/common/img_search.png)
    0 -450px no-repeat;
}
#roadviewControl.active {
    background-position: 0 -350px;
}
#close {
    position: absolute;
    padding: 4px;
    top: 5px;
    left: 5px;
    cursor: pointer;
    background: #fff;
    border-radius: 4px;
    border: 1px solid #c8c8c8;
    box-shadow: 0px 1px #888;
}
#close .img {
    display: block;
    background: url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/rv_close.png)
    no-repeat;
    width: 14px;
    height: 14px;
}